<template>
    <div>
        <v-app-bar
                app
                clipped-right
                color="white"
                light
                flat
        >
            <v-app-bar-nav-icon v-if="window.width > 1024" @click.stop="expand = !expand" />
            <v-app-bar-nav-icon v-else @click.stop="drawer = !drawer" />
            <v-spacer></v-spacer>
            <v-btn to="/" text color="cyan darken-2" v-show="window.width >= 768">Dashboard</v-btn>
            <v-btn to="/testimonial" text color="cyan darken-2" v-show="window.width >= 768" disabled>Testimonial</v-btn>
            <v-btn @click="dialog = !dialog" color="cyan darken-2" class="white--text">Contact Me</v-btn>
        </v-app-bar>
        <v-navigation-drawer
                v-model="drawer"
                :expand-on-hover="expand"
                :mini-variant="expand"
                color="cyan darken-2"
                app
                dark
                style="z-index: 50"
        >
            <v-list dense >
                <v-list-item class="px-2">
                    <v-list-item-content>
                        <v-list-item-title class="cyan--text text--darken-2" style="cursor: default">Yeah you find Me!!</v-list-item-title>
                        <v-list-item-subtitle class="cyan--text text--darken-2" style="cursor: default">NOT ME! SEE ABOVE ME</v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
                <v-list-item
                        v-for="item in items"
                        :key="item.title"
                        :to="item.link"
                        link
                >
                    <v-list-item-icon>
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-item-icon>

                    <v-list-item-content>
                        <v-list-item-title>{{ item.title }}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>
        <v-dialog
                v-model="dialog"
                max-width="290"
        >
            <v-card>
                <v-card-title class="headline">My Contact</v-card-title>

                <v-card-text>
                    <span class="font-weight-bold">Email : </span> julardosatriawan@gmail.com <br>
                    <span class="font-weight-bold">Phone : </span> +62 812 9207 7721
                </v-card-text>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                expand: false,
                drawer: false,
                dialog: false,
                window: {
                    width: 0,
                    height: 0
                },
                items: [
                    {
                        icon: "person",
                        title: "About Me",
                        link: "/"
                    },
                    {
                        icon: "work",
                        title: "Experiences",
                        link: "experiences"
                    },
                    {
                        icon: "list_alt",
                        title: "Projects",
                        link: "projects"
                    },
                    {
                        icon: "build",
                        title: "Skills",
                        link: "skills"
                    }
                ]
            }
        },
        created() {
            window.addEventListener('resize', this.handleResize);
            this.handleResize();
        },
        destroyed() {
            window.removeEventListener('resize', this.handleResize);
        },
        methods: {
            handleResize() {
                this.window.width = window.innerWidth;
                this.window.height = window.innerHeight;
                if(this.window.width > 1024) {
                    this.drawer = true
                }
                else {
                    this.expand = true
                }
            }
        }
    }
</script>